import React, {useEffect, useState} from 'react';
import {Doughnut} from 'react-chartjs-2';

function WorldStats() {
 
    useEffect(() => {
        fetchStats();
      },[]);
    
    //const [isLoading, setIsLoading] = useState(false);
    const [data, setData] = useState([]);
    const [ongoingCases, setOngoingCases] = useState(0);
    const [deaths, setDeaths] = useState(0);
    const [recovered, setRecovered] = useState(0);
    const [total, setTotal] = useState(0);


    const fetchStats = async () => {
        const res = await fetch(`https://cors-anywhere.herokuapp.com/https://coronavirus-19-api.herokuapp.com/all`);
        const j = await res.json();
        
        var ongoing = j.cases - (j.deaths + j.recovered);

        console.log(j);
        const graphData = {
            labels: [
                'Deaths',
                'Recovered',
                'Open Cases'
            ],
            datasets: [{
                data: [j.deaths, j.recovered, ongoing],
                backgroundColor: [
                    '#ff3b3b',
                    '#00b646',
                    '#ffa24c'
                ],
                hoverBackgroundColor: [
                '#ff3b3b',
                '#00b646',
                '#ffa24c'
                ]
            }]
        };

        setOngoingCases(ongoing);
        setDeaths(j.deaths);
        setRecovered(j.recovered);
        setTotal(j.cases);
        setData(graphData);
    }

    return(
        <div className='uk-card uk-card-default'>
            <div className='uk-card-header'>
                <h4 className='uk-card-title'>World Stats</h4>
            </div>
            <div className='uk-card-body uk-flex uk-flex-column uk-text-left'>
                <Doughnut data={data} />
                <span><i className="las la-square-full uk-text-warning"></i>: {ongoingCases.toLocaleString(undefined,{maximumFractionDigits:0})}</span>
                <span><i className="las la-square-full uk-text-success"></i>: {recovered.toLocaleString(undefined,{maximumFractionDigits:0})}</span>
                <span><i className="las la-square-full uk-text-danger"></i>: {deaths.toLocaleString(undefined,{maximumFractionDigits:0})}</span>
                <span>Total: {total.toLocaleString(undefined,{maximumFractionDigits:0})}</span>
            </div>
        </div>
    );
}

export default WorldStats;
