import React, {useContext} from 'react';
import { UpdateContext } from './UpdateProvider.js';
import Update from './Update.js';

function UpdateList() {

  const [updates, setUpdates] = useContext(UpdateContext);

  return (
    <div>
      {updates.map(u => (
        <Update title={u.title} body={u.body} date={u.date} priority={u.priority} source={u.source}/>
      ))}
    </div>  
  );
}

export default UpdateList;
