import React, { useState, createContext } from 'react';

export const UpdateContext = createContext();

export function UpdateProvider(props) {

    const [updates, setUpdates] = useState([
        {
            title: "Philippines under State of Calamity",
            body: "President R. Duterte declares state of calamity for the entire Philippines",
            date: "17/03/2020 7:00PM",
            source: "https://cnnphilippines.com/news/2020/3/17/ph-state-of-calamity-coronavirus.html",
            priority: 0
        },
        {
            title: "Quarantine Status",
            body: `<p>The Philippine government has placed the entire Luzon island under quarantine. Expires 13th of April 12:00AM. Please observe the following guidelines:
            </p>
            <ul>
              <li>Classes and all school activities in all levels are suspended until April 14, 2020. School activities resume 15th April.</li>
              <li>Mass gatherings are prohibited.</li>
              <li>Strict home quarantine must be observed. Movement outside limited to accessing basic necessities like food and health services.
              Heighted presence of uniformed personnel expected to enforce quarantine.
                </li>
              <li>Work-from-home arrangement implemented in the Executive branch except PNP, AFP,PCG, and health and emergency services, border control and other critical services, which shall ensure a skeletal workforce. </li>
              <li>Only private establishments providing basic necessities are allowed to open. BPOs and export oriented industries allowed to
              operate as long as proper distancing measures are observed and their employees are provided with accomodation. Skeletal work force shall be implemented.
              Media personnel shall be allowed to travel within quarantine area provided that they have secured ID cards from PCOO.
                </li>
              <li>Mass public transport suspended.</li>
              <li>Land, air, sea travel restricted.</li>
            </ul>
            To read the full memorandum, click <a href='https://drive.google.com/file/d/1lOQzupmmEusuMhCa1ksPv_6uKfXJIcr4/view' target='_blank' rel="noopener noreferrer">here.</a>`,
            date: "17/03/2020 12:00AM",
            source: "https://drive.google.com/file/d/1lOQzupmmEusuMhCa1ksPv_6uKfXJIcr4/view",
            priority: 1
        },
    ]);

    return(
        <UpdateContext.Provider value={[updates, setUpdates]}>
            {props.children}
        </UpdateContext.Provider>
    );

}