import React from 'react';

function Update({ title, body, date, priority,source }) {
    return (
        <div className='uk-card uk-card-body uk-card-default uk-text-break uk-margin-bottom'>
            <div className='uk-text-left'>
                <h1 className='uk-heading-bullet'>{title}</h1>
                {priority === 1 && (<span class="uk-label uk-label-danger">Important</span>)}
                <p className='uk-text-bold'>{date}</p>
                <p dangerouslySetInnerHTML={{ __html: body }}></p>
                <a class='uk-muted-link' href={source}>Source</a>
            </div>
        </div>
    );
}

export default Update;
