import React, { useEffect, useState } from 'react';


function PhilStats() {
    useEffect(() => {
        fetchWorldStats();
    }, []);

    //const [data, setData] = useState([]);
    const [cases, setCases] = useState(0);
    const [deaths, setDeaths] = useState(0);
    const [recovered, setRecovered] = useState(0);
    const [isLoading, setIsLoading] = useState(true);


    const fetchWorldStats = async () => {
        setIsLoading(true);
        const res = await fetch(`https://cors-anywhere.herokuapp.com/https://coronavirus-19-api.herokuapp.com/countries`);
        const j = await res.json();

        const phl = j.find(item => {
            return item.country == "Philippines";
        })
        setCases(phl.cases);
        setDeaths(phl.deaths);
        setRecovered(phl.recovered);
        setIsLoading(false);
    }

    return (
        <div className='uk-card uk-card-default'>
            <div className='uk-card-header'>
                <h1 className='uk-card-title'>Philippines</h1>
            </div>
            <div className='uk-flex uk-flex-wrap uk-text-center uk-text-large uk-child-width-3@s'>
                <div className='small-box'>
                    <span className=' uk-text-warning uk-text-bold'><i className="las la-notes-medical uk-text-warning"></i>Cases: </span><span className='uk-text-bold'>{isLoading ? <div uk-spinner='true'></div>
                        : (cases)}</span>
                </div>
                <div className='small-box'>
                    <span className=' uk-text-success uk-text-bold'><i className="las la-heart"></i>Recovered: </span><span className='uk-text-bold'>{isLoading ? (<div uk-spinner='true'></div>)
                        : (recovered)}</span>

                </div>
                <div className='small-box'>
                    <span className=' uk-text-danger uk-text-bold'><i className="las la-skull uk-text-danger"></i>Deaths: </span><span className='uk-text-bold'>{isLoading ? (<div uk-spinner='true'></div>) : (deaths)}</span>
                </div>
            </div>
        </div>
    );
}

export default PhilStats;
